import React from 'react';
import Base from './Base';
import {Line} from 'react-chartjs-2';
import Form from 'react-bootstrap/Form';
import { throwStatement } from '@babel/types';
 
class Chart extends Base{
    constructor(props){
        super(props)
        this.state = {
            monthly_payment: 1300,
            interest: 2,
            remaining: 300000,
            amortization_period: 30,
        };
        this.interest_data = []
        this.principal_data = []
        this.computeData()
        this.handleRemainingChange = this.handleRemainingChange.bind(this);
    }
    computeData() {
        var remaining = this.state.remaining
        var interest = this.state.interest
        var amortization_period = this.state.amortization_period
        var monthly_payment = this.state.monthly_payment
        var original_amount = remaining
        var remaining_data = []
        var total_principal_paid = 0
        var total_interest_paid = 0
        var interest_data = []
        for(var run=0;run<=amortization_period;run++){
            this.principal_data.push(total_principal_paid)
            interest_data.push(total_interest_paid)
            var annual_interest_paid = parseFloat(remaining * (interest)/100)
            total_interest_paid = total_interest_paid + annual_interest_paid
            remaining_data.push(remaining)
            total_principal_paid = total_principal_paid + (parseFloat(monthly_payment * 12) - annual_interest_paid )
            if((original_amount - total_principal_paid) < 0)
                break
            remaining = original_amount - total_principal_paid
        }
        this.interest_data = interest_data
        this.remaining_data = remaining_data
    }
    generateLabels(amortization_period){
        var labels = []
        var current_year = parseInt((new Date()).getFullYear());
        for(var run=0;run<=amortization_period;run++){
            labels.push((current_year + run))
        }
        return labels
    }
    generatePrincipalGraph() {
        return {
                label: 'Principal',
                fill: false,
                lineTension: 0.1,
                backgroundColor: 'rgba(63, 191, 127,0.4)',
                borderColor: 'rgba(63, 191, 127,1)',
                borderCapStyle: 'butt',
                borderDash: [],
                borderDashOffset: 0.0,
                borderJoinStyle: 'miter',
                pointBorderColor: 'rgba(63, 191, 127,1)',
                pointBackgroundColor: '#fff',
                pointBorderWidth: 1,
                pointHoverRadius: 5,
                pointHoverBackgroundColor: 'rgba(63, 191, 127,1)',
                pointHoverBorderColor: 'rgba(220,220,220,1)',
                pointHoverBorderWidth: 2,
                pointRadius: 1,
                pointHitRadius: 10,
                data: this.principal_data
        }
    }
    generateRemainingGraph() {
        return {
                label: 'Remaining',
                fill: false,
                lineTension: 0.1,
                backgroundColor: 'rgba(75,192,192,0.4)',
                borderColor: 'rgba(75,192,192,1)',
                borderCapStyle: 'butt',
                borderDash: [],
                borderDashOffset: 0.0,
                borderJoinStyle: 'miter',
                pointBorderColor: 'rgba(75,192,192,1)',
                pointBackgroundColor: '#fff',
                pointBorderWidth: 1,
                pointHoverRadius: 5,
                pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                pointHoverBorderColor: 'rgba(220,220,220,1)',
                pointHoverBorderWidth: 2,
                pointRadius: 1,
                pointHitRadius: 10,
                data: this.remaining_data
        }
    }

    generateInterestGraph() {
        return {
                label: 'Interest',
                fill: false,
                lineTension: 0.1,
                backgroundColor: 'rgba(191, 63, 63,0.4)',
                borderColor: 'rgba(191, 63, 63,1)',
                borderCapStyle: 'butt',
                borderDash: [],
                borderDashOffset: 0.0,
                borderJoinStyle: 'miter',
                pointBorderColor: 'rgba(191, 63, 63,1)',
                pointBackgroundColor: '#fff',
                pointBorderWidth: 1,
                pointHoverRadius: 5,
                pointHoverBackgroundColor: 'rgba(191, 63, 63,1)',
                pointHoverBorderColor: 'rgba(220,220,220,1)',
                pointHoverBorderWidth: 2,
                pointRadius: 1,
                pointHitRadius: 10,
                data: this.interest_data
        }
    }

    generateGraph() {
        var data = {
            labels: this.generateLabels(this.state.amortization_period),
            datasets: [
              this.generateRemainingGraph(),
              this.generateInterestGraph(),
              this.generatePrincipalGraph(),
            ]
        }
        return data
    }

    handleRemainingChange = (e) => {
        var value = e.target.value
        console.log(value)
        this.setState({
            remaining: parseFloat(value)
        });
        this.computeData()
    };

    render() {
        return (
            <div>
                <Form role="form">
                    <div class="row">
                        <Form.Group class="form-group col-lg-1" controlId="formBasicEmail">
                            <div class="mt-3">
                                <Form.Label>Mortgage Amount ($)</Form.Label>
                                <Form.Control value={this.state.remaining} onChange={this.handleRemainingChange} type="text" placeholder="Mortgage Amount" />
                            </div>
                            <div class="mt-3">
                                <Form.Label>Interest Rate (%)</Form.Label>
                                <Form.Control value={this.state.interest.toFixed(2)} class="row mt-3" type="text" placeholder="Interest rate" />
                            </div>
                            <div class="mt-3">
                                <Form.Label>Amortization Period (years)</Form.Label>
                                <Form.Control value={this.state.amortization_period} class="row mt-3" type="text" placeholder="Amortization period" />
                            </div>
                            <div class="mt-3">
                                <Form.Label>Monthly Payment ($)</Form.Label>
                                <Form.Control value={this.state.monthly_payment} class="row mt-3" type="text" placeholder="Monthly payment" />
                            </div>
                        </Form.Group>
                    </div>
                </Form>
                <Line data={this.generateGraph()} />
            </div>
        );
    }
}

export default Chart;
