import React, { Component } from 'react';
import './App.css';
import Chart from './components/Chart';

class App extends Component {

  constructor(props){
  	super(props);
  	this.header = <h1 className="main-header text-center">Mortgage Schedule</h1>
  	this.initializeComponents();
  }

  initializeComponents() {
  	this.components = [];
		this.components['Chart'] = {title:"",renderID:<Chart />}
  }

  renderComponent(componentID) {
  	return	<div className="row">
					<div className="col-lg-12 col-md-12 col-sm-12 text-center mt-3 mb-3">
						{this.components[componentID].title}
        			</div>
        			<div className="col-lg-12 col-md-12 col-sm-12 border-top border-bottom">
        				{this.components[componentID].renderID}
        			</div>
        		</div>
	}
	
  render() {
    return (
      	<div className="container">
					{this.header}
					{this.renderComponent('Chart')}
					<footer className="page-footer font-small blue">
  					<div className="footer-copyright text-center py-3">Â© 2019 Copyright:
    					<a href="http://ahsanmirza.com"> Ahsan mirza</a>
  					</div>
					</footer>
      	</div>
    );
  }
}

export default App;
